
"use client";

import { useState, useEffect, useCallback } from "react";
import type { Promotion } from "@/lib/types";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuTrigger,
  DropdownMenuSeparator,
} from "@/components/ui/dropdown-menu";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import { MoreHorizontal, Pencil, Trash2, PlusCircle, Loader2 } from "lucide-react";
import Image from "next/image";
import PromotionForm from "./promotion-form";
import { useToast } from "@/hooks/use-toast";
import { getPromotions, addPromotion, updatePromotion, deletePromotion } from "@/lib/firebase";

export default function AdminPromotionsTable() {
  const [promotions, setPromotions] = useState<Promotion[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedPromotion, setSelectedPromotion] = useState<Promotion | null>(null);
  const [isFormOpen, setFormOpen] = useState(false);
  const [isDeleteAlertOpen, setDeleteAlertOpen] = useState(false);
  const { toast } = useToast();

  const fetchPromotions = useCallback(async () => {
    setLoading(true);
    try {
        const fetchedPromotions = await getPromotions();
        setPromotions(fetchedPromotions);
    } catch (error) {
        console.error("Failed to fetch promotions:", error);
        toast({ variant: "destructive", title: "Error", description: "No se pudieron cargar las promociones." });
    } finally {
        setLoading(false);
    }
  }, [toast]);

  useEffect(() => {
    fetchPromotions();
  }, [fetchPromotions]);

  const handleOpenForm = (promotion: Promotion | null) => {
    setSelectedPromotion(promotion);
    setFormOpen(true);
  };

  const handleOpenDeleteAlert = (promotion: Promotion) => {
    setSelectedPromotion(promotion);
    setDeleteAlertOpen(true);
  };

  const handlePromotionSave = async (promotionData: Omit<Promotion, 'id'>) => {
     try {
      if (selectedPromotion?.id) {
        // Edit
        await updatePromotion(selectedPromotion.id, promotionData);
        toast({ title: "Promoción Actualizada", description: `La promoción "${promotionData.title}" se ha actualizado.` });
      } else {
        // Add
        await addPromotion(promotionData);
        toast({ title: "Promoción Creada", description: `La promoción "${promotionData.title}" se ha añadido.` });
      }
      await fetchPromotions(); // Refresh list
    } catch (error) {
       toast({ variant: "destructive", title: "Error", description: "No se pudo guardar la promoción." });
       console.error("Failed to save promotion:", error);
    } finally {
        setFormOpen(false);
        setSelectedPromotion(null);
    }
  };

  const handleDeletePromotion = async () => {
    if (selectedPromotion) {
      try {
        await deletePromotion(selectedPromotion.id);
        toast({ title: "Promoción Eliminada", description: `La promoción "${selectedPromotion.title}" se ha eliminado.` });
        await fetchPromotions(); // Refresh list
      } catch (error) {
        toast({ variant: "destructive", title: "Error", description: "No se pudo eliminar la promoción." });
        console.error("Failed to delete promotion:", error);
      } finally {
        setDeleteAlertOpen(false);
        setSelectedPromotion(null);
      }
    }
  };

  if (loading) {
    return (
        <div className="flex justify-center items-center py-10">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
        </div>
    )
  }

  return (
    <div>
      <div className="flex justify-end mb-4">
        <Button onClick={() => handleOpenForm(null)}>
            <PlusCircle className="mr-2 h-4 w-4" />
            Añadir Promoción
        </Button>
      </div>
      <div className="rounded-md border">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="hidden w-[100px] sm:table-cell">
                Imagen
              </TableHead>
              <TableHead>Título</TableHead>
              <TableHead className="hidden md:table-cell">Descripción</TableHead>
              <TableHead>
                <span className="sr-only">Acciones</span>
              </TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {promotions.map((promo) => (
              <TableRow key={promo.id}>
                <TableCell className="hidden sm:table-cell">
                  <Image
                    alt={promo.title}
                    className="aspect-video rounded-md object-cover"
                    height="64"
                    src={promo.imageUrl}
                    width="128"
                    data-ai-hint={promo.imageHint}
                  />
                </TableCell>
                <TableCell className="font-medium">{promo.title}</TableCell>
                <TableCell className="hidden md:table-cell max-w-sm truncate">{promo.description}</TableCell>
                <TableCell>
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button aria-haspopup="true" size="icon" variant="ghost">
                        <MoreHorizontal className="h-4 w-4" />
                        <span className="sr-only">Toggle menu</span>
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <DropdownMenuLabel>Acciones</DropdownMenuLabel>
                      <DropdownMenuItem onClick={() => handleOpenForm(promo)}>
                        <Pencil className="mr-2 h-4 w-4" />
                        Editar
                      </DropdownMenuItem>
                       <DropdownMenuSeparator />
                      <DropdownMenuItem className="text-destructive" onClick={() => handleOpenDeleteAlert(promo)}>
                        <Trash2 className="mr-2 h-4 w-4" />
                        Eliminar
                      </DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

       {/* Form Dialog */}
      <Dialog open={isFormOpen} onOpenChange={setFormOpen}>
        <DialogContent className="sm:max-w-[600px] max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>{selectedPromotion ? 'Editar Promoción' : 'Añadir Nueva Promoción'}</DialogTitle>
            <DialogDescription>
              {selectedPromotion ? 'Modifica los detalles de la promoción.' : 'Completa el formulario para añadir una nueva promoción.'}
            </DialogDescription>
          </DialogHeader>
            <PromotionForm
                promotion={selectedPromotion}
                onSave={handlePromotionSave}
                onCancel={() => {
                  setFormOpen(false);
                  setSelectedPromotion(null);
                }}
            />
        </DialogContent>
      </Dialog>
      
      {/* Delete Confirmation Dialog */}
       <Dialog open={isDeleteAlertOpen} onOpenChange={setDeleteAlertOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>¿Estás seguro que deseas eliminar?</DialogTitle>
            <DialogDescription>
              Esta acción no se puede deshacer. Esto eliminará permanentemente la promoción
              <span className="font-bold"> "{selectedPromotion?.title}"</span>.
            </DialogDescription>
          </DialogHeader>
          <DialogFooter>
            <DialogClose asChild>
                <Button variant="outline">Cancelar</Button>
            </DialogClose>
            <Button variant="destructive" onClick={handleDeletePromotion}>
              Sí, eliminar
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

    </div>
  );
}
